.ideal-image-slider {
  background-color: #fff;
  background-image: url("data:image/gif;base64,");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 32px 32px;
}

.iis-previous-nav, .iis-next-nav {
  z-index: 20;
  width: 60px;
  height: 60px;
  text-indent: -9999px;
  cursor: pointer;
  opacity: 0;
  -o-transition: .3s ease-out;
  background-color: #00000080;
  background-repeat: no-repeat;
  background-size: 48px 48px;
  border-radius: 50px;
  transition: all .3s ease-out;
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.iis-previous-nav {
  background-image: url("data:image/svg+xml;base64,");
  background-position: 35%;
  left: 5%;
}

.iis-next-nav {
  background-image: url("data:image/svg+xml;base64,");
  background-position: 65%;
  right: 5%;
}

.ideal-image-slider:hover .iis-previous-nav, .ideal-image-slider:hover .iis-next-nav {
  opacity: .5;
}

.ideal-image-slider:hover .iis-previous-nav:hover, .ideal-image-slider:hover .iis-next-nav:hover {
  opacity: 1;
}

.iis-bullet-nav {
  z-index: 15;
  width: 90%;
  text-align: right;
  opacity: .4;
  -o-transition: .3s ease-out;
  transition: all .3s ease-out;
  position: absolute;
  bottom: 5%;
  right: 5%;
}

.iis-has-captions .iis-bullet-nav {
  max-width: 42%;
}

.iis-bullet-nav a {
  width: 10px;
  height: 10px;
  text-indent: 9999px;
  cursor: pointer;
  -o-transition: .3s ease-out;
  background: none;
  border: 3px solid #00000080;
  border-radius: 10px;
  margin: 0 5px;
  transition: all .3s ease-out;
  display: inline-block;
}

.iis-bullet-nav a.iis-bullet-active, .iis-bullet-nav a:hover {
  background: #fff;
}

.ideal-image-slider:hover .iis-bullet-nav {
  opacity: .7;
}

.ideal-image-slider:hover .iis-bullet-nav:hover {
  opacity: 1;
}

.iis-has-captions .iis-slide {
  text-indent: 0;
}

.iis-caption {
  max-width: 90%;
  z-index: 10;
  color: #fff;
  box-sizing: border-box;
  background: #00000080;
  border-radius: 10px;
  padding: 5px 15px;
  font: 14px / 1.6em Helvetica Neue, Helvetica, Arial, sans-serif;
  position: absolute;
  bottom: 5%;
  left: 5%;
}

.iis-has-bullet-nav .iis-caption {
  max-width: 42%;
}

.iis-caption .iis-caption-title {
  font-weight: bold;
}

.iis-caption .iis-caption-content {
  color: #eee;
  font-size: 13px;
  line-height: 1.6em;
}

.iis-caption .iis-caption-content a, .iis-caption .iis-caption-content a:visited {
  color: #eee;
  border: 0;
  text-decoration: underline;
}

.iis-caption .iis-caption-content a:hover, .iis-caption .iis-caption-content a:active {
  color: #fff;
}

